*{margin: 0; padding: 0; box-sizing: border-box;}
li{list-style: none;}
a{text-decoration: none;}
button{border: none; background: none; cursor: pointer;}
img{display: block; border: none;}
.wrap{
    width: 1200px;
    display: flex;
    gap: 24px;
    margin: 0 auto;
}
h1, p{
    text-align: center;
}
h1{
    margin-bottom: 10px;
}
p{
    margin-bottom: 40px;
}